  signup(res) {

    const responseFacebook = {
      id: res.userID,
      name: res.name,
      email: res.email,
      token: res.accessToken,
      image: res.picture.data.url,
      providerName: 'Facebook'
    }

    axios.post(process.env.REACT_APP_API_URL + '/Identity/LoginSocialmediaData', responseFacebook)
      .then((result) => {
        if (result.status === 200) {
          const data = result.data.data;

          const token = data.token;
          const jwtParams = JSON.parse(atob(token.split(".")[1]));
          const id = jwtParams.id;
          const isAdmin = jwtParams.role?.toLowerCase().includes("admin");

          let dataToStore = {};
          dataToStore.id = id;
          dataToStore.isAdmin = isAdmin ?? false;
          if (jwtParams.phoneNo) {
            dataToStore.phoneNo = jwtParams.phoneNo;
          }
          localStorage.setItem("user", JSON.stringify(dataToStore));

          var expires = "";

          var date = new Date();
          date.setTime(date.getTime() + (1 * 24 * 60 * 60 * 1000));
          expires = "; expires=" + date.toUTCString();

          document.cookie = "accessToken" + "=" + (token || "") + expires + "; path=/";
          document.cookie = "refreshToken" + "=" + (data.refreshToken || "") + expires + "; path=/";
          document.cookie = "SameSite=None";
          // const [setUser] = useState(null);
          // setUser(dataToStore);
        }

        history.location.state
          ? history.replace(history.location.state)
          : history.push("/");

        toast.success("Bn  ng nhp thnh cng.");
        if (window.location.href.indexOf("sign-in") >= 0) {
          history.push("/");
        }

        window.location.reload();
      }).catch((error) => {
        toast.error(error.message);
      });
  };

  render() {
    const responseFacebook = (response) => {
      this.signup(response);
    }
    return (
      <div className="App">
        <div className="row">
          <div className="col-sm-12 btn">
            <FacebookLogin buttonStyle={{ padding: "6px" }}
              appId="325111522372216"
              autoLoad={true}
              fields="name,email,picture,link"
              scope="public_profile,email"
              version="10.0"
              callback={responseFacebook} />
          </div>
        </div>
      </div>
    )
  }
