import React, { Fragment } from "react";
import { Nav, Navbar, Container, NavDropdown } from "react-bootstrap";
import { history } from "..";
import { useAuth } from "../utils/hooks/authHook";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUser } from "@fortawesome/free-solid-svg-icons";

export const NavMenu = () => {
  const auth = useAuth();

  return (
    <header>
      <Navbar className="orangeBackground" expand="lg">
        <Container>
          <Navbar.Brand href="/">
            <img src="/Logo.png" className="logoSize"></img>
          </Navbar.Brand>
          <Navbar.Toggle />
          <Navbar.Collapse className="collapseBarStyle">
            <Nav className="mr-auto">
              <Nav.Link
                className="whiteColor"
                onClick={() => history.push("/items")}
              >
                Sn Phm
              </Nav.Link>
              {/* <Nav.Link onClick={() => history.push("/contact")}>
                Lin lc vi chng ti
              </Nav.Link> */}
            </Nav>
            {auth.user ? (
              <Nav>
                <NavDropdown
                  title={
                    <FontAwesomeIcon className="whiteColor" icon={faUser} />
                  }
                  style={{ textColor: "white" }}
                >
                  {auth.user.isAdmin ? (
                    <NavDropdown.Item
                      onClick={() => {
                        history.push("/administration");
                      }}
                    >
                      Administration
                    </NavDropdown.Item>
                  ) : (
                    ""
                  )}
                  {auth.user.isAdmin ? (
                    <NavDropdown.Item
                      onClick={() => {
                        history.push("/items/create");
                      }}
                    >
                      To sn phm
                    </NavDropdown.Item>
                  ) : (
                    ""
                  )}
                  <NavDropdown.Divider />
                  <NavDropdown.Item
                    onClick={() => {
                      auth.signOut();
                      history.push("/");
                    }}
                  >
                    ng Xut
                  </NavDropdown.Item>
                </NavDropdown>
              </Nav>
            ) : (
              <Fragment>
                <Nav>
                  {/* <Nav.Link onClick={() => history.push("/sign-up")}>
                    Sign up
                  </Nav.Link> */}
                  <Nav.Link
                    className="whiteColor"
                    onClick={() => history.push("/sign-in")}
                  >
                    ng Nhp
                  </Nav.Link>
                </Nav>
              </Fragment>
            )}
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </header>
  );
};
