import api from "../utils/helpers/api";

const getHighestBid = (itemId) => {
  return api
    .get(`${process.env.REACT_APP_API_BIDS_ENDPOINT}/getHighestBid/${itemId}`)
    .then((response) => response);
};

const getBids = (itemId) => {
  return api.get(`${process.env.REACT_APP_API_BIDS_ENDPOINT}/getBids/${itemId}`)
  .then((response) => response);
}

const updateTelNo = (body) => {
  return api
    .post(process.env.REACT_APP_API_UPDATE_TEL_NO, body)
    .then((response) => response);
};

export default { getHighestBid, getBids, updateTelNo };
