import React, { Fragment } from "react";
import { useState, useEffect } from "react";
import { Result } from "./Result/Result";

export const BidResult = ({ itemId, topBidders, CounterContainer }) => {
  const [messages, setResults] = useState([]);

  const fetchHighestBidder = () => {
    if (topBidders && topBidders.length > 0) {
      setResults(topBidders);
    }
  };

  useEffect(() => {
    fetchHighestBidder();
  }, [itemId, fetchHighestBidder]);

  return (
    <Fragment>
      <Result messages={messages} />
    </Fragment>
  );
};
