import React from "react";
import { useAuth } from "../../../utils/hooks/authHook";
import "./Result.css";
import { faUser } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export const Result = ({ messages }) => {
  const auth = useAuth();

  return (
    <div>
      <h3 className="text-center">Top nhng ngi tr gi cao nht</h3>
      <div className="chat-history">
        <ul className="mx-auto justify-content-center">
          {messages.map((_, index) => {
            let message = messages[messages.length - 1 - index];
            return message.userId === auth?.user?.id ? (
              <li
                key={`${index}-${messages.length}`}
                className={`message ${
                  index === 0 ? "highest-bid-message" : "my-message"
                }`}
              >
                {<img src={message.imageUrl} />}
                {message.name + " "}
                {message.telNo + " "}
                u gi thnh cng
                {message.amount} {process.env.REACT_APP_CURRENCY_SIGN}
              </li>
            ) : (
              <li
                key={index}
                className={`message ${
                  index === 0 ? "highest-bid-message" : "other-message"
                }`}
              >
                {auth?.user?.isAdmin ? (
                  <img src={message.imageUrl} />
                ) : (
                  <FontAwesomeIcon icon={faUser} />
                )}
                {auth?.user?.isAdmin ? message.name + " " : " C ngi "}
                {auth?.user?.isAdmin ? message.telNo + " " : ""}
                u gi cnh tranh 
                {message.amount} {process.env.REACT_APP_CURRENCY_SIGN}: 
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
};
