import React from "react";
import { useAuth } from "../../../utils/hooks/authHook";
import "./Chat.css";
import { faUser } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export const Chat = ({ messages }) => {
  const auth = useAuth();

  return (
    <div className="chat">
      <h3 className="text-center">Nhng tin nhn t h thng.</h3>
      <i>Mc u gi cao nht c hin ln trong nn mu xanh l cy.</i>
      <div className="chat-history">
        <ul className="mx-auto justify-content-center">
          {messages.map((_, index) => {
            let message = messages[messages.length - 1 - index];
            return message.userId === auth.user.id ? (
              <li
                key={`${index}-${messages.length}`}
                className={`message ${
                  index === 0 ? "highest-bid-message" : "my-message"
                }`}
              >
                {auth.user.isAdmin ? <img src={message.image}/>  : <FontAwesomeIcon icon={faUser} />}
                {auth.user.isAdmin ? message.name + " " : "C ngi" } 
                {auth.user.isAdmin ? message.telNo + " " : "" } 
                u gi thnh cng 
                {message.bidAmount} {process.env.REACT_APP_CURRENCY_SIGN}
              </li>
            ) : (
              <li
                key={index}
                className={`message ${
                  index === 0 ? "highest-bid-message" : "other-message"
                }`}
              >
                {auth.user.isAdmin ? <img src={message.image}/>  : <FontAwesomeIcon icon={faUser} />}
                {auth.user.isAdmin ? message.name + " " : "C ngi" } 
                {auth.user.isAdmin ? message.telNo + " " : "" } 
                {process.env.REACT_APP_CURRENCY_SIGN}
                {message.bidAmount}: u gi cnh tranh
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
};
