import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { Form, Button } from "react-bootstrap";
import { history } from "../../../index";
import { toast } from "react-toastify";
import bidService from "../../../services/bidService";

export const UpdateTelNo = () => {
  const { register, handleSubmit, errors, watch, formState } = useForm({
    mode: "onblur",
  });
  const { touched } = formState;

  const onSubmit = (data) => {
    bidService
      .updateTelNo(data)
      .then(() => {
        var user = JSON.parse(localStorage.getItem("user"));
        if (user) {
          user.phoneNo = data.telNo;
          localStorage.setItem("user", JSON.stringify(user));
        }

        history.location.state
          ? history.replace(history.location.state)
          : history.push("/");
        toast.success(
          "Bn  update st thnh cng, xin mi tip tc u gi."
        );
      })
      .catch((error) => {
        toast.error("C li xy ra, xin lin lc vi admin  x l.");
      });
  };

  const hasError = (touched, errors) => {
    if (touched && !errors) {
      return false;
    } else if (touched && errors) {
      return true;
    }
  };

  return (
    <Form noValidate onSubmit={handleSubmit(onSubmit)}>
      <div class="text-center">
        <Form.Group controlId="formBasicTelNo">
          <Form.Label>Nhp S in Thoi</Form.Label>
          <Form.Control 
            name="telNo"
            type="tel"
            placeholder="Cn nhp s in thoi trc khi tham gia u gi"
            ref={register({
              required: "Nhp s in thoi l bt buc",
              pattern: {
                value: /0[0-9\s.-]{9,13}/,
                message: "Xin hy nhp ng s in thoi",
              },
            })}
            isValid={hasError(touched.telNo, errors.telNo) === false ?? true}
            isInvalid={hasError(touched.telNo, errors.telNo)}
          />
          {errors.telNo && (
            <Form.Control.Feedback type="invalid">
              {errors.telNo.message}
            </Form.Control.Feedback>
          )}
        </Form.Group>

        <Button variant="primary" type="submit">
          Gi
        </Button>
      </div>
    </Form>
  );
};
